import React, {useEffect, useState} from 'react';
import { Container,  Image } from 'react-bootstrap';
import { Link } from "react-router-dom";
import useUNST from '../contracts/useUNST';

export default (props)=> {
  const { getUNSTInfo } = useUNST('UNST', 'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce');
  const [ unstInfo, setUnstInfo ] = useState({});

  useEffect(()=>{
    (async()=>{
      getUNSTInfo('0xf47b34463BA3Ee9FB5b0F6Cd2A2222d351fc04CA', v=>{
        console.log(v);
        setUnstInfo(v);
      })
    })()
  }, [])
  return <Container fluid={true} className="p-3 border border-warning rounded bg-tranparent-box"> 
  <h3 className="text-warning">{unstInfo.symbol}<span className="ml-2 p-2" style={{fontSize:'1.1rem'}}>({unstInfo.name})</span></h3>
  <Container fluid={true} className="p-0 pt-1 text-left text-warning">
    <span className="float-left mt-2 p-1 bg-inverse border border-success rounded">Pool: {unstInfo.poolSize}</span>
    <span className="float-left mt-2 ml-3 p-1 bg-inverse border border-success  rounded">Rate  : {unstInfo.rate}</span>
    <span className="float-left mt-2 ml-3 p-1 bg-inverse border border-success rounded">Est. Ethr: {unstInfo.rate}</span>
  </Container>
  <Container className="clearfix"></Container>
</Container>
}